#tic-tac-toe-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: column nowrap;
      flex-flow: column nowrap;
}

#tic-tac-toe-container .row {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: row nowrap;
      flex-flow: row nowrap;
  -ms-flex: 1 1;
      flex: 1 1;
}

#tic-tac-toe-container .row .cell {
  -ms-flex: 1 1;
      flex: 1 1;
  height: 10em;
  width: 10em;
  cursor: pointer;
}

.winning-cell {
  color: #FFFFFF !important;
}

.winning-cell.player1 {
  background-color: #FF4200;
}

.winning-cell.player2 {
  background-color: #0E68FF;
}

.player1 {
  color: #FF4200;
}

.player2 {
  color: #0E68FF;
}

svg {
  height: 100%;
  width: 100%;
  margin: auto;
}

